<transition name="fade"><loading-card v-if="!richMediaData"><h1>Загружаются способы взаимотношения организмов между собой... </h1></loading-card></transition><transition name="fade"><container v-if="richMediaData"><br><h1>Способы взаимотношения организмов между собой</h1><div class="row p-5"><section class="col-sm m-2 p-2" v-for="way in richMediaData"><h1 class="p-2">{{ way.name }}</h1><p class="p-2">{{ way.summary }}</p><br><hr><h1>Примеры:</h1><div class="row p-2" v-for="example in way.examples"><hr><h1>{{ example.name }}</h1><p>{{ example.summary }}</p><img :src="example.avatar"></div></section></div></container></transition>